'useclient'
import Image from 'next/image'
import React, { useState } from 'react'
import './index.css'

type Project = {
  title: string
  img_url: string
}

type Props = {
  projects: Project[]
}

const Carousell = ({ projects }: Props) => {
  const [hoveredIndex, setHoveredIndex] = useState<number | null>(null)

  return (
    <div className='flex gap-1 m-auto w-5/6 items-center justify-center'>
      {projects.map((item, key) => (
        <div
          id='carousell-item'
          key={key}
          className='relative cursor-pointer transition-transform duration-200 hover:scale-105'
          onMouseEnter={() => setHoveredIndex(key)}
          onMouseLeave={() => setHoveredIndex(null)}
        >
          {/* title overlay */}
          {hoveredIndex === key && (
            <div className='absolute top-0 left-0 w-full bg-black/70 text-white text-center py-1 text-sm'>
              {item.title}
            </div>
          )}

          <Image
            height={400}
            width={100}
            src={item.img_url}
            alt={item.title}
            className='object-cover'
          />
        </div>
      ))}
    </div>
  )
}

export default Carousell
